import * as React from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Typography from '@mui/material/Typography';


export default function SignIn() {
  // default signInOpen=false
  const [signInOpen, setSignInOpen] = React.useState(false);
  //const [password, setPassword] = React.useState(false);


  // methods to manipulate the SignIn dialog
  const handleClickSignInOpen = () => {
    setSignInOpen(true);
  };

  const handleSignInClose = () => {
    setSignInOpen(false);
  };

  return (
    <div>
          <Button color="inherit" onClick={handleClickSignInOpen}>Sign In</Button>
          <Dialog open={signInOpen} onClose={handleSignInClose}>
            <DialogTitle>
              <Typography align="center" variant="h6" >
                Sign In
              </Typography>
            </DialogTitle>
            <DialogContent dividers={true}>
              <TextField
                autoFocus
                margin="dense"
                id="userId"
                label="User Id"
                type="email"
                fullWidth
                required
                variant="standard"
              />
              <TextField 
              margin="dense"
              id="password"
              label="Password"
              type="password"
              fullWidth
              required
              variant="standard"
              />
            </DialogContent>
            <DialogActions>
              <Button onClick={handleSignInClose}>Cancel</Button>
              <Button onClick={handleSignInClose}>Sign In</Button>
            </DialogActions>
          </Dialog>
          <SignIn />
        
    </div>

  );
}